<template>
  <div class="update-account">
    <div class="updateform">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>修改账号</span>
        </div>
        <div class="text item">
          <section class="updatelogin">
            <el-form ref="upform" :model="form" :rules="rules" label-width="80px">
              <el-form-item label="账号" prop="name">
                <el-input placeholder="请输入账号" v-model="form.account"></el-input>
              </el-form-item>

              <el-form-item label="用户组" prop="group">
                <el-select v-model="form.userGroup" placeholder="请选择用户组">
                  <el-option label="超级管理员" value="超级管理员"></el-option>
                  <el-option label="部门管理员" value="部门管理员"></el-option>
                  <el-option label="普通管理员" value="普通管理员"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item class="addbtn">
                <el-button type="primary" @click="onSubmit">确认修改</el-button>
                <el-button @click="resetForm">重置</el-button>
              </el-form-item>
            </el-form>
          </section>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  created() {
   this.resetForm()
  },
  data() {
    return {
      form: {
        account: "",
        userGroup: "普通管理员"
      },
      rules: {
        account: [
          { required: true, message: "请输入账户名称", trigger: "blur" },
          { min: 2, max: 5, message: "长度在 2 到 5 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    onSubmit() {
      this.$refs.upform.validate(result => {
        if (result) {
          axios
            .post(
              "http://127.0.0.1:3333/users.php?a=edit",
              `account=${this.form.account}&userGroup=${this.form.userGroup}&id=${this.$route.params.id}`
            )
            .then(data => {
              if (data.data.code == 0) {
                this.$message({
                  message: data.data.msg,
                  type: "success"
                });
                this.$router.push('/index/Account-list')
              } else {
                this.$message.error(data.data.msg);
              }
            });
        } else {
          return false;
        }
      });
    },
    resetForm(){
       axios
      .get(
        `http://127.0.0.1:3333/users.php?a=readOne&id=${this.$route.params.id}`
      )
      .then(data => {
        // console.log(data)
        this.form = data.data.data;
        // console.log(this.form.userGroup);
        // console.log(this.form.account);
      });
    }
  }
};
</script>

<style >
.update-account .updateform {
  margin: 0;
  width: 100% !important;
}

.updatelogin {
  width: 100%;
  background: #ffffff;
}
.updatelogin .el-form {
  width: 300px;
  margin-left: 64px;
}
.updatelogin {
  padding: 30px 0 20px;
}
/* .addlogin .addbtn {
  margin-left: -62px !important;
} */
</style>